import Marquee from "react-fast-marquee";
import ImageWebp from "../../layout/ImageWebp/ImageWebp";

import styles from "./UiUxDesignExamples.module.scss"
import {
    uiUxDesignExampleImg1,
    uiUxDesignExampleImg1Webp,
    uiUxDesignExampleImg2,
    uiUxDesignExampleImg2Webp,
    uiUxDesignExampleImg3,
    uiUxDesignExampleImg3Webp,
    uiUxDesignExampleImg4,
    uiUxDesignExampleImg4Webp,
    uiUxDesignExampleImg5,
    uiUxDesignExampleImg5Webp,
    uiUxDesignExampleImg6,
    uiUxDesignExampleImg6Webp,
    uiUxDesignExampleImg7,
    uiUxDesignExampleImg11,
    uiUxDesignExampleImg7Webp,
    uiUxDesignExampleImg8,
    uiUxDesignExampleImg8Webp,
    uiUxDesignExampleImg10Webp,
    uiUxDesignExampleImg11Webp,
    uiUxDesignExampleImg10,
    uiUxDesignExampleImg9Webp,
    uiUxDesignExampleImg12,
    uiUxDesignExampleImg12Webp,
    uiUxDesignExampleImg9,
} from "../../../assets/images";

const slidesArr = [
    {
        img1: [uiUxDesignExampleImg1, uiUxDesignExampleImg1Webp],
        img2: [uiUxDesignExampleImg2, uiUxDesignExampleImg2Webp]
    },
    {
        img1: [uiUxDesignExampleImg3, uiUxDesignExampleImg3Webp],
        img2: [uiUxDesignExampleImg4, uiUxDesignExampleImg4Webp]
    },
    {
        img1: [uiUxDesignExampleImg5, uiUxDesignExampleImg5Webp],
        img2: [uiUxDesignExampleImg6, uiUxDesignExampleImg6Webp]
    },
    {
        img1: [uiUxDesignExampleImg7, uiUxDesignExampleImg7Webp],
        img2: [uiUxDesignExampleImg8, uiUxDesignExampleImg8Webp]
    },
    {
        img1: [uiUxDesignExampleImg9, uiUxDesignExampleImg9Webp],
        img2: [uiUxDesignExampleImg10, uiUxDesignExampleImg10Webp]
    },
    {
        img1: [uiUxDesignExampleImg11, uiUxDesignExampleImg11Webp],
        img2: [uiUxDesignExampleImg12, uiUxDesignExampleImg12Webp]
    },
]

function UiUxDesignExamples() {
    const slidesArrFirst = slidesArr.map((item) => item.img1)
    const slidesArrSecond = slidesArr.map((item) => item.img2)
    const mobSlidesArr = [...slidesArrFirst, ...slidesArrSecond]

    return (
        <section className={styles['uiUxDesignExamples']}>
            <div className={`${styles['uiUxDesignExamples__container']} container`}>
                <h3 className={`${styles["uiUxDesignExamples__title"]} titleDef`}>Our <span
                    className="lightBlueText">examples</span></h3>
                <div className={`${styles["uiUxDesignExamples__imgList_desk"]} ${styles["uiUxDesignExamples__imgList"]}`}>
                    <Marquee gradient={false} speed={100} className={styles['uiUxDesignExamples__marque']} pauseOnHover={true}>
                        {slidesArr.map((item, index) => (
                            <div className={styles["uiUxDesignExamples__slide"]} key={index}>
                                <ImageWebp
                                    srcSet={item.img1[1]}
                                    src={item.img1[0]}
                                    alt="example"
                                    pictureClass={styles["uiUxDesignExamples__picture"]}
                                    className={styles["uiUxDesignExamples__img"]}/>
                                <ImageWebp
                                    srcSet={item.img2[1]}
                                    src={item.img2[2]}
                                    alt="example"
                                    pictureClass={styles["uiUxDesignExamples__picture"]}
                                    className={styles["uiUxDesignExamples__img"]}/>
                            </div>
                        ))}
                    </Marquee>
                </div>
                <div className={`${styles["uiUxDesignExamples__imgList_mob"]} ${styles["uiUxDesignExamples__imgList"]}`}>
                    <Marquee gradient={false} speed={100} className={styles['uiUxDesignExamples__marque']}>
                        {mobSlidesArr.map((item, index) => (
                            <div className={styles["uiUxDesignExamples__slide"]} key={index}>
                                <ImageWebp
                                    srcSet={item[1]}
                                    src={item[0]}
                                    alt="example"
                                    pictureClass={styles["uiUxDesignExamples__picture"]}
                                    className={styles["uiUxDesignExamples__img"]}/>
                            </div>
                        ))}
                    </Marquee>
                </div>
            </div>
        </section>
    );
}

export default UiUxDesignExamples;